.App {
  background: #212529;
  min-height: 100vh;
}

.loading {
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;
}

.loading > img {
  display: block;
  width: auto;
  height: 35px;
  margin-left: 15px;
}

.section-title {
  color: white;
}

.section-text {
  color: rgb(221, 221, 221);
  margin-top: 15px;
}

.link {
  text-decoration: none;
}

.link:hover {
  text-decoration: underline;
}
