.experience {
  min-height: 100vh;
  background: #212529;
  padding-top: 100px;
}

.nav-pills .nav-link {
  color: rgb(207, 207, 207) !important;
}

.nav-pills .nav-link:hover {
  background-color: #343a41 !important;
}

.nav-pills .active {
  border: none;
  border-radius: 0 !important;
  background-color: #343a41 !important;
  color: #0dcaf0 !important;
}

ul.timeline {
  list-style-type: none;
  position: relative;
}
ul.timeline:before {
  content: " ";
  background: #343a41;
  display: inline-block;
  position: absolute;
  left: 29px;
  width: 2px;
  height: 100%;
  z-index: 400;
}
ul.timeline > li {
  margin: 20px 10px;
  padding-left: 20px;
}
ul.timeline > li:before {
  content: " ";
  background: #343a41;
  display: inline-block;
  position: absolute;
  border-radius: 50%;
  border: 2px solid #0dcaf0;
  left: 20px;
  width: 20px;
  height: 20px;
  z-index: 400;
}
